export default {
	props: {
		order: {
			type: Object,
		}
	},
	data() {
		return {
			orderCounts: 1
		}
	},
	methods: {
		reduceorderCounts: function() {
			if (this.orderCounts > 1) {
				this.orderCounts -= 1
				this.$emit('changeAllMoney',this.orderCounts)
			}
		},
		addorderCounts: function() {
			if(this.orderCounts<=this.order.count){
				this.orderCounts += 1
				this.$emit('changeAllMoney',this.orderCounts)
			}
		}
	}
}
